.c-fileUpload {
  position: relative;
  min-height: 200px;
  border: 1px solid #78706A;
  border-radius: 4px;
  background-color: whitesmoke;
  cursor: pointer;
}
.c-fileUpload:hover {
  background-color: rgba(0, 255, 0, 0.1);
}
.c-fileUpload-placeholder {
  padding: 5px;
  width: 100%;
}
.c-fileUpload-placeholder-message {
  top: 0.25rem;
  position: absolute;
  text-align: center;
  width: 90%;
}
.c-fileUpload-placeholder-status {
  right: 12px;
  bottom: 0.25rem;
  position: absolute;
  font-style: italic;
  font-size: 1rem;
  text-align: right;
}
.c-fileUpload-placeholder-status-OK {
  color: #78706A;
}
.c-fileUpload-placeholder-status-ERROR {
  color: red;
}
.c-fileUpload-files {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  padding: 0.5rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.c-fileUpload-active {
  background-color: rgba(0, 128, 0, 0.2);
  animation: c-fileUpload-pulsate 1s ease-out;
  animation-iteration-count: infinite;
}
.c-fileUpload .scrollable {
  overflow-x: auto;
  white-space: nowrap;
}
.c-fileUpload .fileUploadFile-exit {
  opacity: 0.01;
  transform: scale(0.1);
  transition: all 500ms ease-out;
}
.c-fileUpload .fileUploadFile-exit-active {
  opacity: 0.01;
  transform: scale(0.1);
  transition: all 500ms ease-out;
}
@keyframes c-fileUpload-pulsate {
  0% {
    background-color: rgba(0, 128, 0, 0.2);
  }
  50% {
    background-color: rgba(0, 128, 0, 0.1);
  }
  100% {
    background-color: rgba(0, 128, 0, 0.2);
  }
}
.highContrast .c-fileUpload {
  background-color: #000000;
}
