.c-file {
  position: relative;
}
.c-file.animate {
  animation: c-file-FadeIn 0.4s;
}
.c-file .overlay {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.c-file .top-overlay {
  position: absolute;
  top: 0px;
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
}
.c-file .link {
  z-index: 10;
  background-color: white;
  padding: 1px 5px;
  border-radius: 3px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.c-file .link:hover {
  background-color: lightblue;
}
.c-file .previousPage {
  left: 0px;
}
.c-file .nextPage {
  right: 0px;
}
.c-file .previousPage,
.c-file .nextPage {
  text-decoration: none;
  font-size: 1.2rem;
  position: absolute;
  top: 45%;
  z-index: 2;
  -webkit-text-stroke: 2px white;
}
.c-file .previousPage:hover,
.c-file .nextPage:hover {
  text-decoration: none;
}
.c-file .pageNumber {
  position: absolute;
  bottom: 0px;
  right: 0px;
  z-index: 2;
  background-color: white;
  padding: 0px 3px;
}
@keyframes c-file-FadeIn {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  60% {
    opacity: 1;
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
